"use client"

import { useState } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "../ui/Card"
import { Button } from "../ui/Button"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "../ui/tabs"
import { Upload, ExternalLink, Calendar, Settings } from "lucide-react"

type TestType = "SAT" | "ACT" | "AP Tests"

interface ScoreData {
  date: string
  score: number
}

interface APScore {
  subject: string
  score: number
}

export default function TestPrepDashboard() {
  const [selectedTest, setSelectedTest] = useState<TestType>("SAT")

  // Sample score progression data
  const satScoreHistory: ScoreData[] = [
    { date: "Jan", score: 1000 },
    { date: "Feb", score: 1150 },
    { date: "Mar", score: 1280 },
    { date: "Apr", score: 1380 },
    { date: "May", score: 1480 },
  ]

  const actScoreHistory: ScoreData[] = [
    { date: "Jan", score: 20 },
    { date: "Feb", score: 23 },
    { date: "Mar", score: 26 },
    { date: "Apr", score: 29 },
    { date: "May", score: 32 },
  ]

  const apScores: APScore[] = [
    { subject: "AP Biology", score: 4 },
    { subject: "AP Calculus AB", score: 5 },
    { subject: "AP English Literature", score: 3 },
    { subject: "AP US History", score: 4 },
    { subject: "AP Chemistry", score: 5 },
  ]

  const currentSATScore = 1100
  const targetSATScore = 1400
  const currentACTScore = 24
  const targetACTScore = 32
  const nextTestDate = "March 9, 2024"

  // Calculate progress percentage for circles
  const satCurrentProgress = (currentSATScore / 1600) * 100
  const satTargetProgress = (targetSATScore / 1600) * 100
  const actCurrentProgress = (currentACTScore / 36) * 100
  const actTargetProgress = (targetACTScore / 36) * 100

  const resources = [
    { name: "Khan Academy", type: "Free Practice", icon: "ðŸ“š" },
    { name: "College Board SAT Question Bank", type: "Official Questions", icon: "ðŸŽ¯" },
    { name: "Blue Method SAT Prep", type: "Prep Course", icon: "ðŸ“–" },
  ]

  const renderScoreChart = (scoreHistory: ScoreData[], maxScore: number, minScore: number) => {
    const chartWidth = 400
    const chartHeight = 200
    const padding = 40

    return (
      <div className="relative h-64 mb-6 bg-white rounded-lg border p-4">
        <svg className="w-full h-full" viewBox={`0 0 ${chartWidth} ${chartHeight + 40}`}>
          {/* Y-axis labels and grid lines */}
          {[0, 0.25, 0.5, 0.75, 1].map((ratio, index) => {
            const y = chartHeight - ratio * (chartHeight - padding * 2) - padding
            const scoreValue = Math.round(minScore + (maxScore - minScore) * ratio)
            return (
              <g key={index}>
                <line x1={padding} y1={y} x2={chartWidth - padding} y2={y} stroke="#e5e7eb" strokeWidth="1" />
                <text x={padding - 10} y={y + 4} textAnchor="end" className="text-xs fill-gray-500">
                  {scoreValue}
                </text>
              </g>
            )
          })}

          {/* Score line */}
          <polyline
            fill="none"
            stroke="#14b8a6"
            strokeWidth="3"
            points={scoreHistory
              .map((point, index) => {
                const x = padding + (index * (chartWidth - padding * 2)) / (scoreHistory.length - 1)
                const y =
                  chartHeight -
                  padding -
                  ((point.score - minScore) / (maxScore - minScore)) * (chartHeight - padding * 2)
                return `${x},${y}`
              })
              .join(" ")}
          />

          {/* Data points */}
          {scoreHistory.map((point, index) => {
            const x = padding + (index * (chartWidth - padding * 2)) / (scoreHistory.length - 1)
            const y =
              chartHeight - padding - ((point.score - minScore) / (maxScore - minScore)) * (chartHeight - padding * 2)
            return <circle key={index} cx={x} cy={y} r="4" fill="#14b8a6" />
          })}

          {/* X-axis labels */}
          <text x={padding} y={chartHeight + 20} textAnchor="middle" className="text-xs fill-gray-500">
            Initial
          </text>
          <text x={chartWidth - padding} y={chartHeight + 20} textAnchor="middle" className="text-xs fill-gray-500">
            Latest
          </text>

          {/* Settings icon */}
          <g transform={`translate(${chartWidth - 30}, ${chartHeight - 10})`}>
            <rect width="20" height="20" rx="4" fill="#f3f4f6" />
            <Settings className="w-3 h-3" x="8.5" y="8.5" />
          </g>
        </svg>
      </div>
    )
  }

  const renderAPBarChart = () => {
    return (
      <div className="space-y-4">
        {apScores.map((ap, index) => (
          <div key={index} className="space-y-2">
            <div className="flex justify-between items-center">
              <span className="text-sm font-medium text-gray-700">{ap.subject}</span>
              <span className="text-sm font-bold text-gray-900">{ap.score}/5</span>
            </div>
            <div className="w-full bg-gray-200 rounded-full h-3">
              <div
                className="bg-gradient-to-r from-teal-400 to-teal-600 h-3 rounded-full transition-all duration-300"
                style={{ width: `${(ap.score / 5) * 100}%` }}
              ></div>
            </div>
          </div>
        ))}
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50 p-6">
      <div className="max-w-6xl mx-auto">
        {/* Header Tabs */}
        <Tabs value={selectedTest} onValueChange={(value) => setSelectedTest(value as TestType)} className="w-full">
          <TabsList className="grid w-full max-w-md grid-cols-3 bg-white shadow-sm">
            <TabsTrigger value="SAT" className="data-[state=active]:bg-teal-500 data-[state=active]:text-black">
              SAT
            </TabsTrigger>
            <TabsTrigger value="ACT" className="data-[state=active]:bg-teal-500 data-[state=active]:text-black">
              ACT
            </TabsTrigger>
            <TabsTrigger value="AP Tests" className="data-[state=active]:bg-teal-500 data-[state=active]:text-white">
              AP Tests
            </TabsTrigger>
          </TabsList>

          <TabsContent value="SAT" className="mt-6">
            <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
              {/* Left Column - Main Content */}
              <div className="lg:col-span-2 space-y-6">
                {/* Test Score Metrics */}
                <Card className="bg-white shadow-sm">
                  <CardHeader>
                    <CardTitle className="text-lg font-semibold text-gray-800">Test Score Metrics</CardTitle>
                    <p className="text-sm text-gray-500">SAT score improvement over time</p>
                  </CardHeader>
                  <CardContent>
                    {renderScoreChart(satScoreHistory, 1600, 800)}

                    {/* Score Circles */}
                    <div className="flex justify-center gap-12">
                      {/* Current Score Circle */}
                      <div className="flex flex-col items-center">
                        <div className="relative w-24 h-24 mb-3">
                          <svg className="w-24 h-24 transform -rotate-90" viewBox="0 0 36 36">
                            <path
                              d="M18 2.0845 a 15.9155 15.9155 0 0 1 0 31.831 a 15.9155 15.9155 0 0 1 0 -31.831"
                              fill="none"
                              stroke="#e5e7eb"
                              strokeWidth="3"
                            />
                            <path
                              d="M18 2.0845 a 15.9155 15.9155 0 0 1 0 31.831 a 15.9155 15.9155 0 0 1 0 -31.831"
                              fill="none"
                              stroke="#10d9c4"
                              strokeWidth="3"
                              strokeDasharray={`${satCurrentProgress}, 100`}
                            />
                          </svg>
                          <div className="absolute inset-0 flex items-center justify-center">
                            <span className="text-xl font-bold text-gray-800">{currentSATScore}</span>
                          </div>
                        </div>
                        <div className="text-center">
                          <div className="text-sm font-medium text-gray-700">Reading, Writing and Language</div>
                          <div className="text-xs text-gray-500">Current Average</div>
                        </div>
                      </div>

                      {/* Target Score Circle */}
                      <div className="flex flex-col items-center">
                        <div className="relative w-24 h-24 mb-3">
                          <svg className="w-24 h-24 transform -rotate-90" viewBox="0 0 36 36">
                            <path
                              d="M18 2.0845 a 15.9155 15.9155 0 0 1 0 31.831 a 15.9155 15.9155 0 0 1 0 -31.831"
                              fill="none"
                              stroke="#e5e7eb"
                              strokeWidth="3"
                            />
                            <path
                              d="M18 2.0845 a 15.9155 15.9155 0 0 1 0 31.831 a 15.9155 15.9155 0 0 1 0 -31.831"
                              fill="none"
                              stroke="#10d9c4"
                              strokeWidth="3"
                              strokeDasharray={`${satTargetProgress}, 100`}
                            />
                          </svg>
                          <div className="absolute inset-0 flex items-center justify-center">
                            <span className="text-xl font-bold text-gray-800">{targetSATScore}</span>
                          </div>
                        </div>
                        <div className="text-center">
                          <div className="text-sm font-medium text-gray-700">Reading, Writing and Language</div>
                          <div className="text-xs text-gray-500">Target Score</div>
                        </div>
                      </div>
                    </div>
                  </CardContent>
                </Card>
              </div>

              {/* Right Column - Next Test and Resources */}
              <div className="space-y-4">
                {/* Next Test Header */}
                <div className="bg-white rounded-lg shadow-sm p-4">
                  <div className="flex items-center justify-between">
                    <div>
                      <h3 className="text-sm font-semibold text-gray-800">Next Test</h3>
                      <div className="flex items-center gap-2 text-gray-600 mt-1">
                        <Calendar className="w-3 h-3" />
                        <span className="text-xs">{nextTestDate}</span>
                      </div>
                    </div>
                    <Button size="sm" className="bg-teal-500 hover:bg-teal-600 text-white text-xs px-3 py-1">
                      Book now
                    </Button>
                  </div>
                </div>

                {/* Resources Card */}
                <Card className="bg-white shadow-sm">
                  <CardHeader className="pb-3">
                    <CardTitle className="text-lg font-semibold text-gray-800">Resources</CardTitle>
                  </CardHeader>
                  <CardContent className="space-y-3">
                    {resources.map((resource, index) => (
                      <div
                        key={index}
                        className="flex items-center justify-between p-3 bg-gray-50 rounded-lg hover:bg-gray-100 transition-colors"
                      >
                        <div className="flex items-center gap-3">
                          <span className="text-lg">{resource.icon}</span>
                          <div>
                            <div className="font-medium text-gray-800 text-sm">{resource.name}</div>
                            <div className="text-xs text-gray-500">{resource.type}</div>
                          </div>
                        </div>
                        <ExternalLink className="w-4 h-4 text-gray-400" />
                      </div>
                    ))}
                  </CardContent>
                </Card>

                {/* Upload Test Card */}
                <Card className="bg-white shadow-sm">
                  <CardHeader className="pb-3">
                    <CardTitle className="text-lg font-semibold text-gray-800">Upload Test</CardTitle>
                  </CardHeader>
                  <CardContent>
                    <div className="border-2 border-dashed border-gray-300 rounded-lg p-6 text-center hover:border-teal-400 transition-colors">
                      <Upload className="w-8 h-8 text-gray-400 mx-auto mb-3" />
                      <p className="text-sm text-gray-600 mb-2">Let AI analyze your practice tests</p>
                      <p className="text-xs text-gray-500 mb-4">
                        Upload a PDF of your practice test results to get personalized insights and recommendations to
                        improve your score.
                      </p>
                      <Button
                        variant="outline"
                        size="sm"
                        className="border-teal-500 text-teal-600 hover:bg-teal-50 bg-transparent"
                      >
                        <Upload className="w-4 h-4 mr-2" />
                        Choose File
                      </Button>
                    </div>
                  </CardContent>
                </Card>
              </div>
            </div>
          </TabsContent>

          <TabsContent value="ACT" className="mt-6">
            <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
              {/* Left Column - Main Content */}
              <div className="lg:col-span-2 space-y-6">
                {/* Test Score Metrics */}
                <Card className="bg-white shadow-sm">
                  <CardHeader>
                    <CardTitle className="text-lg font-semibold text-gray-800">Test Score Metrics</CardTitle>
                    <p className="text-sm text-gray-500">ACT score improvement over time</p>
                  </CardHeader>
                  <CardContent>
                    {renderScoreChart(actScoreHistory, 36, 10)}

                    {/* Score Circles */}
                    <div className="flex justify-center gap-12">
                      {/* Current Score Circle */}
                      <div className="flex flex-col items-center">
                        <div className="relative w-24 h-24 mb-3">
                          <svg className="w-24 h-24 transform -rotate-90" viewBox="0 0 36 36">
                            <path
                              d="M18 2.0845 a 15.9155 15.9155 0 0 1 0 31.831 a 15.9155 15.9155 0 0 1 0 -31.831"
                              fill="none"
                              stroke="#e5e7eb"
                              strokeWidth="3"
                            />
                            <path
                              d="M18 2.0845 a 15.9155 15.9155 0 0 1 0 31.831 a 15.9155 15.9155 0 0 1 0 -31.831"
                              fill="none"
                              stroke="#10d9c4"
                              strokeWidth="3"
                              strokeDasharray={`${actCurrentProgress}, 100`}
                            />
                          </svg>
                          <div className="absolute inset-0 flex items-center justify-center">
                            <span className="text-xl font-bold text-gray-800">{currentACTScore}</span>
                          </div>
                        </div>
                        <div className="text-center">
                          <div className="text-sm font-medium text-gray-700">English, Math, Reading, Science</div>
                          <div className="text-xs text-gray-500">Current Average</div>
                        </div>
                      </div>

                      {/* Target Score Circle */}
                      <div className="flex flex-col items-center">
                        <div className="relative w-24 h-24 mb-3">
                          <svg className="w-24 h-24 transform -rotate-90" viewBox="0 0 36 36">
                            <path
                              d="M18 2.0845 a 15.9155 15.9155 0 0 1 0 31.831 a 15.9155 15.9155 0 0 1 0 -31.831"
                              fill="none"
                              stroke="#e5e7eb"
                              strokeWidth="3"
                            />
                            <path
                              d="M18 2.0845 a 15.9155 15.9155 0 0 1 0 31.831 a 15.9155 15.9155 0 0 1 0 -31.831"
                              fill="none"
                              stroke="#10d9c4"
                              strokeWidth="3"
                              strokeDasharray={`${actTargetProgress}, 100`}
                            />
                          </svg>
                          <div className="absolute inset-0 flex items-center justify-center">
                            <span className="text-xl font-bold text-gray-800">{targetACTScore}</span>
                          </div>
                        </div>
                        <div className="text-center">
                          <div className="text-sm font-medium text-gray-700">English, Math, Reading, Science</div>
                          <div className="text-xs text-gray-500">Target Score</div>
                        </div>
                      </div>
                    </div>
                  </CardContent>
                </Card>
              </div>

              {/* Right Column - Next Test and Resources */}
              <div className="space-y-4">
                {/* Next Test Header */}
                <div className="bg-white rounded-lg shadow-sm p-4">
                  <div className="flex items-center justify-between">
                    <div>
                      <h3 className="text-sm font-semibold text-gray-800">Next Test</h3>
                      <div className="flex items-center gap-2 text-gray-600 mt-1">
                        <Calendar className="w-3 h-3" />
                        <span className="text-xs">{nextTestDate}</span>
                      </div>
                    </div>
                    <Button size="sm" className="bg-teal-500 hover:bg-teal-600 text-white text-xs px-3 py-1">
                      Book now
                    </Button>
                  </div>
                </div>

                {/* Resources Card */}
                <Card className="bg-white shadow-sm">
                  <CardHeader className="pb-3">
                    <CardTitle className="text-lg font-semibold text-gray-800">Resources</CardTitle>
                  </CardHeader>
                  <CardContent className="space-y-3">
                    {resources.map((resource, index) => (
                      <div
                        key={index}
                        className="flex items-center justify-between p-3 bg-gray-50 rounded-lg hover:bg-gray-100 transition-colors"
                      >
                        <div className="flex items-center gap-3">
                          <span className="text-lg">{resource.icon}</span>
                          <div>
                            <div className="font-medium text-gray-800 text-sm">{resource.name}</div>
                            <div className="text-xs text-gray-500">{resource.type}</div>
                          </div>
                        </div>
                        <ExternalLink className="w-4 h-4 text-gray-400" />
                      </div>
                    ))}
                  </CardContent>
                </Card>

                {/* Upload Test Card */}
                <Card className="bg-white shadow-sm">
                  <CardHeader className="pb-3">
                    <CardTitle className="text-lg font-semibold text-gray-800">Upload Test</CardTitle>
                  </CardHeader>
                  <CardContent>
                    <div className="border-2 border-dashed border-gray-300 rounded-lg p-6 text-center hover:border-teal-400 transition-colors">
                      <Upload className="w-8 h-8 text-gray-400 mx-auto mb-3" />
                      <p className="text-sm text-gray-600 mb-2">Let AI analyze your practice tests</p>
                      <p className="text-xs text-gray-500 mb-4">
                        Upload a PDF of your practice test results to get personalized insights and recommendations to
                        improve your score.
                      </p>
                      <Button
                        variant="outline"
                        size="sm"
                        className="border-teal-500 text-teal-600 hover:bg-teal-50 bg-transparent"
                      >
                        <Upload className="w-4 h-4 mr-2" />
                        Choose File
                      </Button>
                    </div>
                  </CardContent>
                </Card>
              </div>
            </div>
          </TabsContent>

          <TabsContent value="AP Tests" className="mt-6">
            <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
              {/* Left Column - Main Content */}
              <div className="lg:col-span-2 space-y-6">
                {/* AP Test Scores */}
                <Card className="bg-white shadow-sm">
                  <CardHeader>
                    <CardTitle className="text-lg font-semibold text-gray-800">AP Test Scores</CardTitle>
                    <p className="text-sm text-gray-500">Your AP exam results by subject</p>
                  </CardHeader>
                  <CardContent>{renderAPBarChart()}</CardContent>
                </Card>

                {/* AP Score Summary */}
                <Card className="bg-white shadow-sm">
                  <CardHeader>
                    <CardTitle className="text-lg font-semibold text-gray-800">Score Summary</CardTitle>
                  </CardHeader>
                  <CardContent>
                    <div className="grid grid-cols-5 gap-4 text-center">
                      {[1, 2, 3, 4, 5].map((score) => {
                        const count = apScores.filter((ap) => ap.score === score).length
                        return (
                          <div key={score} className="p-4 bg-gray-50 rounded-lg">
                            <div className="text-2xl font-bold text-gray-800">{count}</div>
                            <div className="text-sm text-gray-600">Score {score}</div>
                          </div>
                        )
                      })}
                    </div>
                  </CardContent>
                </Card>
              </div>

              {/* Right Column - Next Test and Resources */}
              <div className="space-y-4">
                {/* Next Test Header */}
                <div className="bg-white rounded-lg shadow-sm p-4">
                  <div className="flex items-center justify-between">
                    <div>
                      <h3 className="text-sm font-semibold text-gray-800">Next Test</h3>
                      <div className="flex items-center gap-2 text-gray-600 mt-1">
                        <Calendar className="w-3 h-3" />
                        <span className="text-xs">May 6-17, 2024</span>
                      </div>
                    </div>
                    <Button size="sm" className="bg-teal-500 hover:bg-teal-600 text-white text-xs px-3 py-1">
                      Register
                    </Button>
                  </div>
                </div>

                {/* Resources Card */}
                <Card className="bg-white shadow-sm">
                  <CardHeader className="pb-3">
                    <CardTitle className="text-lg font-semibold text-gray-800">Resources</CardTitle>
                  </CardHeader>
                  <CardContent className="space-y-3">
                    {[
                      { name: "AP Classroom", type: "Official Resources", icon: "ðŸŽ“" },
                      { name: "College Board AP Practice", type: "Practice Exams", icon: "ðŸ“" },
                      { name: "AP Study Guides", type: "Review Materials", icon: "ðŸ“š" },
                    ].map((resource, index) => (
                      <div
                        key={index}
                        className="flex items-center justify-between p-3 bg-gray-50 rounded-lg hover:bg-gray-100 transition-colors"
                      >
                        <div className="flex items-center gap-3">
                          <span className="text-lg">{resource.icon}</span>
                          <div>
                            <div className="font-medium text-gray-800 text-sm">{resource.name}</div>
                            <div className="text-xs text-gray-500">{resource.type}</div>
                          </div>
                        </div>
                        <ExternalLink className="w-4 h-4 text-gray-400" />
                      </div>
                    ))}
                  </CardContent>
                </Card>

                {/* Upload Test Card */}
                <Card className="bg-white shadow-sm">
                  <CardHeader className="pb-3">
                    <CardTitle className="text-lg font-semibold text-gray-800">Upload Test</CardTitle>
                  </CardHeader>
                  <CardContent>
                    <div className="border-2 border-dashed border-gray-300 rounded-lg p-6 text-center hover:border-teal-400 transition-colors">
                      <Upload className="w-8 h-8 text-gray-400 mx-auto mb-3" />
                      <p className="text-sm text-gray-600 mb-2">Upload AP practice tests</p>
                      <p className="text-xs text-gray-500 mb-4">
                        Get detailed analysis of your AP practice test performance and targeted study recommendations.
                      </p>
                      <Button
                        variant="outline"
                        size="sm"
                        className="border-teal-500 text-teal-600 hover:bg-teal-50 bg-transparent"
                      >
                        <Upload className="w-4 h-4 mr-2" />
                        Choose File
                      </Button>
                    </div>
                  </CardContent>
                </Card>
              </div>
            </div>
          </TabsContent>
        </Tabs>
      </div>
    </div>
  )
}
